import React, { ReactNode } from 'react';
import { Container, makeStyles, Theme, createStyles } from '@material-ui/core';
import { useSpring, animated } from 'react-spring';
import Header from 'components/Header';
import Footer from 'components/Footer';
import SEO from 'components/Utils/SEO';

const useStyles = makeStyles<Theme>(theme =>
  createStyles({
    root: {
      backgroundColor: theme.palette.background.default,
    },
    container: {
      marginTop: '3rem',
      marginBottom: '3rem',
    },
    title: {
      color: theme.palette.text.primary,
    },
    description: {
      color: theme.palette.text.secondary,
      whiteSpace: 'pre-line',
    },
    fab: {
      position: 'fixed',
      bottom: 20,
      right: 20,
      zIndex: 2,
    },
  })
);

interface Props {
  children: ReactNode;
  pageName: string;
  showTitle?: boolean;
  description?: ReactNode;
  hideFooter?: boolean;
}
const Page = ({ children, pageName, showTitle = false, description, hideFooter = false }: Props) => {
  const styles = useStyles();
  const titleAnimation = useSpring({
    from: { opacity: 0, transform: 'translateX(-30px)' },
    to: { opacity: 1, transform: 'translateX(0)' },
  });
  const descriptionAnimation = useSpring({
    from: { opacity: 0, transform: 'translateX(30px)' },
    to: { opacity: 1, transform: 'translateX(0)' },
  });

  return (
    <div data-layout="page">
      <SEO title={pageName} />
      <Header />
      <div data-page={pageName} className={styles.root}>
        {showTitle ? (
          <Container className={styles.container}>
            <animated.h1 style={titleAnimation} className={styles.title}>
              {pageName}
            </animated.h1>
            {description && (
              <animated.p style={descriptionAnimation} className={styles.description}>
                {description}
              </animated.p>
            )}
          </Container>
        ) : null}
        <main>{children}</main>
      </div>
      {!hideFooter && <Footer />}
    </div>
  );
};

export default Page;
