import React from 'react';
import { Link } from 'gatsby';
import { Grid, makeStyles, Theme, createStyles, useMediaQuery, useTheme } from '@material-ui/core';
import { GlobalMenus } from 'constants/menu';
import RouteLink from 'components/Utils/RouteLink';
import { getLogoSrc } from 'utils/logo';

const useStyles = makeStyles<Theme>(theme =>
  createStyles({
    root: {
      padding: '1rem',
    },
    menus: {
      display: 'flex',
      padding: 0,
      margin: 0,
      alignItems: 'center',
      height: '100%',
    },
    menuItems: {
      fontSize: '1rem',
      listStyle: 'none',
      marginRight: theme.spacing(2),
      [theme.breakpoints.down('md')]: {
        fontSize: '0.8rem',
        marginRight: theme.spacing(1),
      },
    },
    logo: {
      width: 130,
      [theme.breakpoints.down('md')]: {
        width: 40,
      },
    },
    link: {
      color: theme.palette.text.secondary,
      textDecoration: 'none',
      transition: 'color 0.2s ease-in',
      '&:hover': {
        color: theme.palette.primary.main,
      },
      [theme.breakpoints.down('md')]: {
        fontSize: '0.7rem',
      },
    },
  })
);

const Header = () => {
  const styles = useStyles();
  const theme = useTheme();
  const matches = useMediaQuery(theme.breakpoints.down('md'));
  const logoSrc = getLogoSrc(theme.palette.type, matches);

  return (
    <header className={styles.root}>
      <Grid justify="space-between" alignItems="center" container>
        <Grid xs="auto" item>
          <Link to="/">
            <img className={styles.logo} src={logoSrc} alt="Î£®ÎπÑÏΩò Î°úÍ≥†" />
          </Link>
        </Grid>
        <Grid xs="auto" item>
          <ul className={styles.menus}>
            {GlobalMenus.map((menu, index) => (
              <li key={index} className={styles.menuItems}>
                <RouteLink to={menu.link} className={styles.link}>
                  {menu.name}
                </RouteLink>
              </li>
            ))}
          </ul>
        </Grid>
      </Grid>
    </header>
  );
};

export default Header;
