import { ASSETS_HOST } from 'src/constants';
import { PaletteType } from '@material-ui/core';

export function getLogoSrc(themeType: PaletteType = 'light', isMobile = false) {
  const logoType = isMobile ? 'symbol' : 'logo';
  const colorType = themeType === 'dark' ? 'light' : 'color';
  return `${ASSETS_HOST}/logo/${logoType}-${colorType}.svg`;
}
